var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_vm._ssrNode("<section class=\"conditions\"><h1>CONDITIONS GÉNÉRALES DE VENTE - LES PROFESSIONNELS :</h1> <p>\n      Les présentes conditions générales sont applicables à toutes ventes de\n      produits ou réalisation d’une Prestation de service de Traiteur par la\n      société́ MyTerroir auprès de ses Clients professionnels. Toute commande\n      emporte adhésion et acceptation pleine et entière des présentes\n      Conditions Générales et Particulières de Vente par le Client, ce qui est\n      expressément reconnu par ce dernier, qui renonce, notamment, à se\n      prévaloir de tout document contradictoire, qui serait inopposable à\n      MyTerroir. Le client garantit qu’il dispose de la pleine capacité́\n      juridique pour pouvoir conclure des contrats et qu’il a au moins 18 ans\n      révolus, qu’il réside en France et qu’il contracte à titre\n      professionnel.\n    </p> <h2>1 - DEFINITIONS</h2> <p>\n      Pour les besoins des présentes conditions générales, les mots suivants\n      débutants par une lettre majuscule auront la signification qui leur est\n      attribuée ci-dessous :\n    </p> <p>\n      Le ou les « Client(s) » : Ce terme désigne toute personne physique ou\n      morale, commerçante, désirant faire l’acquisition des Prestations\n      fournies par MyTerroir à titre professionnel.\n    </p> <h2>2 - LE DEVIS</h2> <p>\n      L’émission de l’offre de restauration reprend l’ensemble du cahier des\n      charges du Client soit la définition du menu, le nombre de convives, la\n      date, les horaires, les prestations annexes demandées ainsi que toutes\n      particularités techniques indispensables à la bonne exécution de la\n      manifestation. La composition de toutes ces données est formalisée par\n      un devis ainsi qu’un prix. Le devis est susceptible d’ajustement suite\n      au repérage du lieu de l’évènement : étage, distance, puissance\n      électrique, équipement de l’office, parking, gestion des déchets,\n      protection des sols, vaisselle imposée par le lieu. Cette liste est non\n      exhaustive.\n    </p> <p>\n      Les prix indiqués sur le devis sont valables pour une période de 3 mois.\n    </p> <h2>3 - LE CONTRAT</h2> <p>\n      L’objet du contrat de nature commerciale consiste, en principal, en la\n      fourniture de repas et, à titre accessoire, en la fourniture de\n      prestations annexes. Pour la bonne exécution du contrat, le Client\n      s’engage à fournir a MyTerroir, les informations nécessaires à la bonne\n      exécution de sa prestation : le programme détaillé́, le plan\n      d’installation des espaces, le détail des services et prestations\n      sollicitées par ailleurs.\n    </p> <p>\n      Le devis établi par MyTerroir constitue les conditions particulières\n      venant compléter les présentes conditions générales. Tous litiges\n      relatifs au présent contrat seront de la compétence exclusive du\n      Tribunal de Commercede Bordeaux. La constitution du contrat est\n      formalisée par l’acceptation écrite par le Client donneur d’ordre, en\n      premier lieu, de la dernière version du devis émis par MyTerroir, en\n      deuxième lieu, des présentes conditions générales.\n    </p> <p>\n      La commande est ferme, définitive et non susceptible d’annulation de\n      part comme de l’autre des deux parties à compter de la réception de sa\n      confirmation.\n    </p> <h2>4 - LA COMMANDE</h2> <h3>4.1. FORMALITE DE VALIDATION</h3> <p>\n      Le Client s’engage dès la signature du contrat à préciser le nombre de\n      repas au plus juste qui devront être fournis en vertu du pacte national\n      de lutte contre le gaspillage alimentaire dont le réseau des Traiteurs\n      de France est signataire.\n    </p> <p>\n      Il est de la responsabilité́ du Client de vérifier l’exhaustivité́ et\n      l’exactitude des informations reportées sur le devis. La dernière\n      version du devis validée provenant du Client fait état de référence,\n      MyTerroir ne pourra être tenu responsable d’une erreur validée par le\n      Client. Pour être recevable, toute confirmation de commande doit être\n      adressée par écrit 20 jours calendaires avant le jour de la\n      manifestation. Le Client s’engage à fournir a MyTerroir gracieusement\n      les fluides nécessaires et notamment l’eau et l’électricité́ à la\n      puissance nécessaire à la bonne réalisation de la Prestation (qui sera\n      précisé́ dans le devis).\n    </p> <p>\n      Les éventuelles modifications de la commande demandées par le Client ne\n      seront prises en compte, dans la limite des possibilités de MyTerroir,\n      que si elles sont notifiées par écrit, 4 jours au moins avant la date\n      prévue pour la fourniture des prestations de services commandées, après\n      signature par le Client d'un nouveau bon de commande spécifique et\n      ajustement éventuel du prix. Toutefois ce délai sera porté à 10 jours\n      dans l’hypothèse d’une modification du volume de la Prestation supérieur\n      à 10 %. Le devis est susceptible d’ajustement suite au repérage du lieu\n      de l’évènement : étage, distance, puissance électrique, équipement de\n      l’office, parking, gestion des déchets, protection des sols, vaisselle\n      imposée par le lieu. Cette liste est non exhaustive.\n    </p> <p>\n      La confirmation est effectuée exclusivement par le Client donneur\n      d’ordre et payeur. La validation de commande est matérialisée par\n      l’ensemble des prestations intégrées dans le devis, de ce fait\n      l’ensemble du devis ainsi que les conditions générales devront être\n      signés, la dernière page du devis intégrant le récapitulatif budgétaire\n      global du budget devra être datée, signée, tamponnée, avec les mentions\n      : nom, prénom et qualité́ du signataire, « bon pour accord » ainsi que\n      les numéros de SIRET et TVA Intracommunautaire. Acomptes de réservation\n      : Toute réservation doit être accompagnée du versement d’un acompte\n      correspondant à 80 % du montant du devis total TTC. Le versement de\n      l’acompte déclenche une préparation optimale de l’évènement (réservation\n      du matériel, du personnel de service, achats des matières premières,\n      boissons et éventuelles prestations sous-traitées) ; le versement se\n      décompose comme suit : 30 % à la réservation et 50 % 1 mois avant\n      l’évènement. Le solde devra se faire dans les 15 jours à réception de la\n      facture. Si les acomptes ne sont pas perçus en totalité́ dans un délai de\n      10 jours ouvrés avant la réception, nous nous réservons le droit\n      d’annuler la commande sans qu’aucune indemnité́ pour préjudice ne soit\n      réclamée par le Client. La Clientèle étrangère devra s’acquitter d’un\n      versement d’un acompte de 100 % du montant total soit 60 % à la\n      réservation et 40 % au minimum 10 jours ouvrés avant l’évènement.\n    </p> <h3>4-2 EXECUTION DU CONTRAT</h3> <p><strong>a/</strong> Informations : MyTerroir fera son affaire de\n      communiquer à ses préposés et intervenants y compris, les modalités\n      d’exécution établies en amont par le Client. Il est du devoir du Client\n      de prévenir MyTerroir de toutes modifications et/ou précisions\n      contractuelles, notamment celles afférentes à la logistique, dans les\n      conditions et selon les modalités stipulées à l’article 4 ci-après.\n    </p> <p><strong>b/</strong> Délégation de pouvoir : Le Client donneur d’ordre\n      pourra designer la personne référente sur place le jour J. Le fait pour\n      le Client de contracter avec un organisateur ne modifiera en aucun cas\n      ces dites conditions, l’organisateur doit assumer l’ensemble des\n      obligations du Client et devra pour ce faire avoir été mandaté par le\n      Client et agir au nom et pour le compte de ce dernier.\n    </p> <p><strong>c/</strong> Responsabilité́ du Client : Le Client reconnait être\n      parfaitement informé et fera son affaire des différents agréments et\n      autorisations administratifs que nécessitent éventuellement\n      l’organisation, l’ouverture au public et le déroulement de sa\n      manifestation. Le Client sera seul responsable des perturbations du\n      service de MyTerroir dans le cadre de changement d’horaire lors du\n      déroulement de l’évènement.\n    </p> <p>\n      Le Client s’engage à prendre toutes dispositions notamment en matière\n      d’assurances pour couvrir tous les risques de toutes natures inhérents à\n      l’évènement, objet de la commande, et susceptibles de sécuriser les\n      biens des personnes. Les frais de remise en état des lieux de réception\n      sont pris en charge par le Client. Aucune réclamation sur le déroulement\n      de la réception ne sera recevable postérieurement au jour de celle-ci.\n      Toute perte, vol, dégradation, disparition, casse du matériel mise à la\n      disposition des Clients n’étant pas inclus dans le tarif du Traiteur,\n      seront facturés à leurs prix de remplacement.\n    </p> <p>\n      Le Client organisateur est seul responsable des agissements des\n      participants à l’évènement, objet de la commande, il fera son affaire de\n      toute question tenant à la sécurité́ des personnes et des biens, aux\n      lieux ou la manifestation doit se dérouler, à la vérification des\n      identités, à l’âge ou à la qualité́ des personnes, ainsi qu’à\n      l’autorisation, à la régulation de la consommation des boissons\n      alcoolisées, en aucun cas la responsabilité́ ne pourra être recherchée de\n      ces chefs.\n    </p> <p><strong>d/</strong> Qualité́ et hygiène des aliments : Les livraisons\n      sont effectuées par nos soins, en véhicule frigorifique à température\n      règlementaire entre 0 °C et 2 °C. Les produits alimentaires sont à\n      conserver entre 0° et 4°C (0° et 2°C pour les produits contenant des\n      produits de la mer crus). Sauf indication contraire, les produits livrés\n      dans le cadre de la Prestation et notamment dans le cadre de la\n      distribution de plateau repas sont élaborées pour être dégustées froids.\n      Le produit doit être consommé dans les 24h après la livraison, dans des\n      conditions optimales de conservation. Suite au décret n° 2002-1465 du\n      14/12/02, nous vous informons que les viandes bovines sont d'origine :\n      UE. Il appartient au Client de s’assurer que des moyens techniques sont\n      à disposition sur le site pour conserver les produits dans de bonnes\n      conditions de température. Dans l’éventualité́ où le stockage au froid\n      n’est pas possible, les produits alimentaires frais doivent être\n      consommés dans les 2h et avoir été maintenus à une température à cœur\n      inferieure ou égale à + 2°C durant tout ce délai. Si le respect de ce\n      délai de 2h hors froid n’est pas envisageable, le Traiteur peut\n      également conserver un camion frigorifique sur place pour conserver les\n      produits à la demande du Client. Ce service n’est pas systématiquement\n      possible et sera facturé au Client. Si ces conditions de gestion de la\n      température ne sont pas respectées, MyTerroir décline toute\n      responsabilité́ en cas de problème de santé lié à la consommation des\n      produits. Pour des raisons d’hygiène et de sécurité́ alimentaire, aucune\n      marchandise livrée conforme à la commande ne pourra être reprise ou\n      échangée. Par ailleurs, à l’issue de la prestation, notre personnel de\n      service a pour consigne de détruire toutes marchandises restantes. Le\n      Traiteur se dégage de toute responsabilité sur les risques encourus dans\n      le cas où le Client insisterait pour conserver les produits restants.\n      Une décharge devra, en pareil cas, être signée par le Client.\n    </p> <p><strong>e/</strong> Allergènes : Nos produits contiennent pour certains\n      des allergènes. La liste des allergènes contenus dans des ingrédients\n      volontairement incorporés à la recette est à la disponibilité́ du Client\n      à sa demande. Outre, les allergènes volontairement ajoutés, il n’est pas\n      exclu que nos produits contiennent des traces d’un ou plusieurs des\n      allergènes majeurs cités dans l’annexe II du règlement UE 1169/2011. En\n      effet, malgré́ toutes les précautions prises durant la préparation, des\n      contaminations croisées fortuites sont possibles.\n    </p> <p><strong>f/</strong> Tri sélectif : Notre engagement en matière de tri,\n      valorisation et recyclage des déchets engendrés par la prestation\n      nécessite la mise à disposition par le Client, de contenants de tri\n      sélectifs et un service d’évacuation des déchets. Dans le cas où le\n      Client ne serait pas en mesure d’assurer sur site, la gestion des\n      déchets, MyTerroir en assurera la gestion (tri, stockage, évacuation) et\n      des frais supplémentaires s’appliqueront. Des frais seront également à\n      la charge du Client, dans le cas où d’éventuels déchets tels que\n      bouteilles en verre, cartons, décoration fournis par le Client ou\n      d’autres Traiteurs sont évacués par les soins de MyTerroir.\n    </p> <p><strong>g/</strong> Lutte contre le gaspillage : En cas d’excédents\n      alimentaires d’un volume significatif, de produits ayant été conservés\n      dans de bonnes conditions de températures (aucune rupture de la chaîne\n      du froid), d’un commun accord avec le Client, un don alimentaire peut\n      être effectué.\n    </p> <p><strong>h/</strong> Dépassements horaires : Pour tout service au-delà̀\n      des horaires convenus entre les parties, un supplément pour le personnel\n      sera facturé, le coût de l’heure supplémentaire par personnel est\n      indiqué́ dans le devis. Les heures supplémentaires débuteront à la fin de\n      la vacation du personnel. La vacation du personnel inclue le temps de\n      remise en état des lieux de réception, de rechargement et de transport,\n      toute heure commencée sera facturée en heure pleine.\n    </p> <h3>4-3.LIVRAISON</h3> <p>\n      Aucune livraison ne peut intervenir en l’absence du Client ou d’un\n      réceptionnaires mandaté pour recevoir la commande. L’exactitude et la\n      précision des coordonnées de livraison ainsi que la disponibilité́ du\n      réceptionnaire sont essentielles, tout retard ou erreur ne pourra être\n      imputé au Traiteur, en cas d’informations erronées ou incomplètes. Les\n      délais prévus dans la livraison d’une commande ne sont donnés qu’à titre\n      indicatif et les retards éventuels ou avances dans l’horaire prévu\n      inferieur à 2 heures ne donnent pas droit au Client d’annuler la\n      commande, de refuser la marchandise ou de réclamer des dommages et\n      intérêts. En cas de redirection de la commande vers une autre adresse de\n      livraison, des frais de livraison supplémentaires s’appliqueront. Toute\n      commande doit être reçue par le Client et contrôlée de manière\n      contradictoire avec le chauffeur au moment de la livraison. Toute\n      réclamation devra être mentionnée, sur le bon de livraison. Les mentions\n      « réserves » ou « sous réserve de déballage » sont dépourvues de portée.\n      En cas de non-conformité́ à la commande ou de défectuosité́, notre\n      garantie se limite au remplacement des marchandises reconnues non\n      conformes ou défectueuses, à l’exclusion de toute autre indemnité́.\n    </p> <h3>4-4.FACTURATION</h3> <p>\n      La facture sera adressée dans les 72 heures après l’évènement intégrant\n      les consommations supplémentaires de boissons, les possibles heures\n      supplémentaires de service et la perte et casse de la vaisselle, du\n      nappage et du matériel mis à votre disposition par MyTerroir. Les prix\n      indiqués sur le devis correspondent au tarif applicable lors de\n      l’établissement du devis, ils s’entendent HT, sous réserve du taux en\n      vigueur lors de la réception. Compte tenu de la nature des produits que\n      nous commercialisons en circuit court ou assujettis à des hausses de\n      prix du marché́ (produits frais, alimentaires et périssables) nos tarifs\n      sont susceptibles d’évoluer et d’être modifiés sans préavis. Dans le cas\n      d’un changement de législation, les tarifs seront automatiquement\n      réajustés, par exemple, en cas de variation de taux ou taxes et feront\n      l’objet de la révision de notre devis. Toute rétrocommission pour les\n      sites de réception ou agences évènementielles sera facturée au Client.\n      Nos tarifs intègrent la mise à disposition par les sites de réception\n      des offices, des flux et du dispositif de réception de notre matériel\n      ainsi que la mise en avant de notre savoir-faire du fait de notre\n      référencement. Toute rétrocommission ne pourra être pris en compte à\n      partir de la signature du contrat par le donneur d’ordre.\n    </p> <h2>5 - CONDITIONS DE MODIFICATION</h2> <h3>5-1. NOMBRE DE PERSONNE</h3> <p>\n      Le devis initialement établi par MyTerroir et accepté par le Client a\n      été spécifiquement établi pour un nombre de participants à l’évènement\n      commandé par le Client. Pour être recevable, toute demande de\n      modification du nombre de participants doit nous parvenir 4 jours ouvrés\n      avant la date de la manifestation et doit donner lieu à une acceptation\n      écrite du Traiteur. Une nouvelle version du bon de commande modifié sera\n      alors établie et devra être validée par le même Client donneur d’ordre.\n      Si le nombre définitif devait augmenter dans les 4 jours ouvrés\n      précédant la réception, nous ne pourrions assurer les repas qu’en\n      fonction de nos approvisionnements et nous réservons le droit de\n      remplacer les produits tout en gardant la même qualité́.\n    </p> <h3>5-2.ANNULATION</h3> <p>\n      Dans l’hypothèse où le Client déciderait d’annuler sa commande pour\n      quelque cause que ce soit, il se verrait soumis au versement d’un dédit\n      suivant l’échéancier suivant : Annulation entre 30 et 60 jours\n      calendaires avant la date de réception : 15 % du montant TTC total du\n      devis. Annulation entre 20 et 30 jours calendaires avant la date de\n      réception : 40 % du montant TTC total du devis. Annulation entre 10 et\n      20 jours calendaires avant la date de réception : 60 % du montant TTC\n      total du devis. Annulation entre 5 et 10 jours calendaires avant la date\n      de réception : 80 % du montant TTC total du devis. Annulation à moins de\n      5 jours calendaires avant la date de réception : 100 % du montant TTC\n      total du devis.\n    </p> <h3>5-3.REPORT</h3> <p>\n      En cas de report de date de la prestation commandée, pour quelques\n      raisons que ce soit y compris en cas de force majeure, l’acompte versé\n      restera acquis a MyTerroir et ne donnera lieu à aucune répétition. Dans\n      l’hypothèse ou l’évènement commandé ne pourrait se tenir à la date\n      convenue, un avoir correspondant au montant de l’acompte versé serait\n      adressé au Client, valable sur une durée de 12 mois suivant son\n      émission. Passé ce délai, la manifestation sera considérée comme\n      définitivement annulé et les sommes versées resteront acquises au\n      Traiteur. Un complément d’acompte pourra, le cas échéant, être réclamé́\n      en cas de révision non seulement de la date, du lieu mais également de\n      la composition de la commande, en pareil cas, un avenant au contrat sera\n      établi, le Traiteur se réserve le droit de modifier le prix unitaire de\n      vente et de facturer au Client les frais techniques, administratifs et\n      de dédommagement auprès de ses propres prestataires.\n    </p> <h2>6 - FORCE MAJEURE</h2> <p>\n      MyTerroir est dégagé́ de plein droit de ses obligations sans paiement\n      d’indemnité́ d’aucune sorte en cas de survenance d’un évènement de force\n      majeure ou cas fortuit, notamment en cas de guerre, émeute, blocus grève\n      totale ou partielle, incendie, fermeture administrative par décision de\n      l’état, blocage des moyens de transport, crise sanitaire ou tout autre\n      évènement extérieur susceptible de perturber la bonne circulation des\n      marchandises. De même, la responsabilité́ de MyTerroir ne saurait être\n      engagée pour tout inconvénient ou dommage inhérente à l’utilisation\n      d’internet, notamment une rupture de service, une intrusion extérieure\n      ou un virus informatique.\n    </p> <p>\n      De convention expresse et déterminante à la conclusion du contrat, dans\n      l’hypothèse où la prestation commandée ne pourrait se dérouler à date\n      convenue en raison d’un cas de force majeure, l’acompte versé resterait\n      acquis a MyTerroir, dans les conditions et modalités fixées à l’article\n      5-3 susvisé́ ; un avoir correspondant au montant de l’acompte versé\n      valable sur une durée de 12 mois suivant son émission serait en pareil\n      cas adressé au Client, à l’exclusion de tout remboursement.\n    </p> <h2>7 - PROTECTION INTELLECTUELLE</h2> <p>\n      Nous vous rappelons que tout organisateur d’évènement doit s’acquitter\n      des droits de propriété́ intellectuelle lies à son évènement. Les\n      propositions ainsi que les photos, vidéos, plans, supports, maquettes,\n      documents et informations de toutes natures remis ou adressés par\n      MyTerroir au Client demeurent la propriété́ de celui- ci, de ce fait ils\n      ne peuvent être communiqués à des tiers sous quelques motifs que se\n      soient. Sauf interdiction formelle du Client, MyTerroir se réserve le\n      droit d’utiliser des photos de l’évènement, hors personnages et\n      références au nom du Client ou des participants, pour sa communication\n      personnelle (plaquette, presse, réseaux sociaux, internet etc...).\n    </p> <h2>8 - DONNEES PERSONNELLES ET PROTECTION DE LA VIE PRIVEE</h2> <p>\n      Dans le cadre de ses relations contractuelles, MyTerroir est amené à\n      collecter des données à caractère personnel nécessaire au traitement de\n      la commande. Conformément aux dispositions légales et règlementaires\n      applicables, en particulier la loi n° 78-17 du 6 janvier 1978 modifiée\n      relative à l'informatique, aux fichiers et aux libertés et du règlement\n      européen n°2016/679/UE du 27 avril 2016, le Client bénéficie d’un droit\n      d’accès, de rectification, de portabilité́ et d’effacement de ses données\n      ou encore de limitation du traitement. Il peut également, pour des\n      motifs légitimes, s’opposer au traitement des données le concernant.\n      Pour exercer ces droits et obtenir des informations détaillées sur les\n      durées de conservation des données, le Client doit contacter MyTerroir à\n      l’adresse suivante : 482 rue du courdouney, 33140 Cadaujac ou par\n      internet à l’adresse suivante traiteur@my-terroir.fr. Les informations\n      collectées ont pour finalité́ le suivi descommandes, la gestion de la\n      relation Client et, si l’utilisateur y a consenti, la prospection\n      Clients. L’accès à vos données personnelles est strictement limité au\n      personnel de MyTerroir. Aucun profilage ne sera réalisé́ et plus\n      généralement aucune décision automatisée ne sera prise sur la base des\n      données collectées. Nous nous engageons à ne pas vendre, louer, céder ni\n      donner accès à des tiers aux données du Client sans son consentement\n      préalable, à moins d’y être contraint en raison d’un motif légitime\n      (obligation légale, lutte contre la fraude, exercice des droits de la\n      défense, etc.). Le Client est informé qu'il dispose du droit\n      d'introduire une action auprès de la Commission Nationale de\n      l'Informatique et des Libertés. Dans le cas de l’utilisation d’une photo\n      de l’évènement à destination des réseaux sociaux du Traiteur ou du\n      Client, elle fera l’objet d’une validation mutuelle par écrit.\n    </p> <h2>9 - LES CONDITIONS DE PAIEMENT</h2> <p>\n      Le solde du prix TTC est réglable à réception de facture. Les\n      instructions particulières de facturation devront être communiquées au\n      moment du passage de la commande. Toute demande de modification\n      d’éléments de facturation (avoir et refacturation) ou de réédition\n      (duplicata) doit être demandée dans les 24 heures suivant la réception\n      de la facture. Le taux de pénalités exigible en cas de retard de\n      paiement sera égal au taux d'intérêt légal appliqué par la banque\n      centrale européenne majoré de 7 points et une indemnité́ forfaitaire pour\n      frais de recouvrement, d'un montant de 40€ sera due de plein droit et\n      sans notification préalable. Nous nous réservons le droit de demander au\n      Client, une indemnisation complémentaire si les frais de recouvrement\n      dépassaient ce montant. Toute facture présentée à la livraison sans\n      acompte doit être payée comptant, sauf accord écrit préalable à défaut,\n      nous nous réservons le droit de rapporter la marchandise en nos locaux.\n      Conformément aux dispositions de l’article L 4431 du code du commerce,\n      le délai de règlement de nos factures ne peut excéder 30 jours après la\n      fin de la décade de la livraison ou à date de facture, par virement ou\n      par chèque bancaire. Tout règlement par chèque doit être libellé à\n      l’ordre de MYTERROIR. Aucun escompte ne sera accordé́ pour paiement\n      anticipé.\n    </p> <h1>CONDITIONS PARTICULIÈRES DE VENTE - LES PROFESSIONNELS :</h1> <h2>1 - REPAS DEGUSTATION</h2> <p>\n      Afin que le Client valide l’offre commerciale, un test de dégustation\n      pourra être réalisé́. Le client s’engage à respecter les conditions\n      suivantes :\n    </p> <ul><li>\n        Commander le test de dégustation pour un nombre maximum de 4 personnes\n        (5 si présence d’une agence qui serait l’invité de MyTerroir)\n      </li> <li>\n        Valider au préalable l’enveloppe budgétaire et le contenu technique du\n        devis\n      </li> <li>\n        Établir avec MyTerroir la sélection des articles à présenter,\n        représentative de l’offre et raisonnablement compatible avec une\n        approche responsable contre le gaspillage alimentaire et le respect de\n        la saisonnalité́ de produits\n      </li> <li>\n        Fixer le test de dégustation dans un délai raisonnable autorisant sa\n        bonne préparation\n      </li> <li>\n        Transmettre en amont les informations essentielles à MyTerroir pour\n        préparer au mieux sa dégustation et notamment :\n        <p><strong>Le nom des autres Maisons testées</strong></p> <p><strong>Le nom et la fonction des participants</strong></p> <p><strong>Les critères principaux sur lesquels le client fera son\n            choix</strong></p></li></ul> <p>\n      MyTerroir assurera la réalisation du test de dégustation demandé contre\n      la participation aux coûts inhérents à sa mise en œuvre à hauteur :\n    </p> <p>\n      D’un forfait de 50 € HT/ par personne. Cette participation sera déduite\n      de la facture finale si MyTerroir est sélectionné́ pour réaliser la\n      prestation. Ces frais de participation devront être versés à MyTerroir\n      par le Client avant la date du test de dégustation. Dans le cas où le\n      client souhaiterait organiser un deuxième test de dégustation complet,\n      les conditions de participation seront identiques à celles édictées pour\n      une première dégustation, sans déduction sur la facture finale. Dans le\n      cas où le Client annulerait un test de dégustation dans un délai ne\n      permettant pas à sa déprogrammation, ce test de dégustation fera l’objet\n      d’une participation facturée au Client sur la base des ressources\n      mobilisées et incompressibles.\n    </p> <h2>2 - MENUS SPECIFIQUES</h2> <p>\n      Si le nombre définitif devait augmenter dans les 4 jours ouvrés\n      précèdent la réception, nous ne pourrions assurer les repas qu’en\n      fonction de nos approvisionnements et nous réservons le droit de\n      remplacer les produits tout en gardant la même qualité́. Néanmoins, une\n      majoration de 20% du prix unitaire de ces repas supplémentaires sera\n      appliquée en vertu des coûts supplémentaires (Production, matériel\n      etc...). En vertu des coûts supplémentaires induits par leur production,\n      les menus spécifiques (vegan, végétarien, allergiques etc...) seront\n      majorés également de 20%.\n    </p> <h2>3 - GESTION DES BOISSONS</h2> <h3>3-1. BOISSONS FOURNIES PAR LE CLIENT</h3> <p>\n      Dans le cas où, avec l’accord du Traiteur, le Client fournirait les\n      boissons, celles-ci doivent être livrées et récupérées obligatoirement\n      sur le lieu de la réception, à défaut de quoi nous serions obligés de\n      facturer des frais de manutention et de transport.\n    </p> <h3>3-2. GESTION DES BOUTEILLES VIDES</h3> <p>\n      Dans le cas où une benne à verre ne serait pas mise à disposition sur le\n      lieu de réception et dans le cas d’une reprise de ces bouteilles vides\n      par nos soins, un forfait comprenant la remise en carton, le transport,\n      le déchargement et le tri dans nos locaux de ces bouteilles sera\n      appliqué :\n      <br>\n      - 0 à 50 bouteilles 50€HT <br>\n      - 50 à 100 bouteilles : 90€HT <br>\n      - 100 bouteilles et + : 180€HT\n    </p> <h3>3-3 BOISSONS SPECIFIQUES</h3> <p>\n      • Les préparations maison, cocktails de jus de fruits frais, vins &amp;\n      boissons étrangers ou prévus pour une occasion spécifique et non\n      consommés ne seront pas défalqués de la facturation finale. <br>\n      • Dans le cas de commande de boissons spécifiques hors de notre carte,\n      celles-ci vous seront facturées dans la globalité́ et selon le packaging\n      en vigueur pour ces boissons.\n    </p> <h2>4 - GESTION DES DECHETS</h2> <p>\n      Des bennes de tri sélectifs doivent être mises à disposition du Traiteur\n      sur le lieu de réception. Dans le cas où le Traiteur devrait, avec son\n      accord, gérer les déchets du Client ou d’autres prestataires, celui- ci\n      se verrait dans l’obligation de facturer des frais de gestion\n      supplémentaires.\n    </p> <h2>5 - DEVIS / NOMBRE DE PERSONNES</h2> <p>\n      Le devis en votre possession a été réalisé́ par rapport à un nombre de\n      personnes précis, si celui-ci venait à être modifié, un nouveau devis\n      sera réalisé́.\n    </p> <h1>CONDITIONS GÉNÉRALES DE VENTE - LES PARTICULIERS :</h1> <p>\n      Les présentes conditions générales ont pour objet de régir les relations\n      entre MyTerroir et le client désigné́ comme donneur d’ordre.\n    </p> <p>\n      En passant commande, le client déclare expressément avoir pris\n      connaissance des conditions générales de vente de MyTerroir et en\n      accepter l’ensemble des dispositions sans réserve, ni restriction.\n    </p> <h2>1. DEFINITIONS</h2> <p>\n      Pour les besoins des présentes conditions générales, les mots suivants\n      débutants par une lettre majuscule auront la signification qui leur est\n      attribuée ci-dessous :\n    </p> <p>\n      Le ou les « Client(s) » : Ce terme désigne toute personne physique ou\n      morale, particulier, désirant faire l’acquisition des Prestations\n      fournies par MyTerroir au titre du particulier.\n    </p> <h2>2. LE DEVIS</h2> <p>\n      L’émission de l’offre de restauration reprend l’ensemble du cahier des\n      charges du client soit la définition du menu, le nombre de convives, la\n      date, les horaires, les prestations annexes demandées ainsi que toutes\n      particularités techniques indispensables à la bonne exécution de la\n      manifestation. La composition de toutes ces données est formalisée par\n      un devis ainsi qu’un prix. Le devis est susceptible d’ajustement suite\n      au repérage du lieu de l’évènement : étage, distance, puissance\n      électrique, équipement de l’office, parking, gestion des déchets,\n      protection des sols, vaisselle imposée par le lieu. Cette liste est non\n      exhaustive.\n    </p> <p>\n      Les prix indiqués sur le devis sont valables pour une période de 3 mois.\n    </p> <h2>3. LE CONTRAT</h2> <p>\n      L’objet du contrat de nature commerciale consiste, en principal, en la\n      fourniture de repas et, à titre accessoire, en la fourniture de\n      prestations annexes. Pour la bonne exécution du contrat, le client\n      s’engage à fournir a MyTerroir les informations nécessaires à la bonne\n      exécution de sa prestation : le programme détaillé́, le plan\n      d’installation des espaces, le détail des services et prestations\n      sollicitées par ailleurs. Le devis établi par MyTerroir constitue les\n      conditions particulières venant compléter les présentes conditions\n      générales.\n    </p> <p>\n      Tous litiges relatifs au présent contrat seront de la compétence\n      exclusive du Tribunal de Commerce de Bordeaux. La constitution du\n      contrat est formalisée par l’acceptation écrite par le client donneur\n      d’ordre, en premier lieu, de la dernière version du devis émis par\n      MyTerroir, en deuxième lieu, des présentes conditions générales. La\n      commande est ferme, définitive et non susceptible d’annulation de part\n      comme de l’autre des deux parties à compter de la réception de sa\n      confirmation.\n    </p> <h2>4. LA COMMANDE</h2> <h3>4-1.FORMALITES DE VALIDATION</h3> <p>\n      Le client s’engage dès la signature du contrat à préciser le nombre de\n      repas au plus juste qui devront être fournis en vertu du pacte national\n      de lutte contre le gaspillage alimentaire dont le réseau des traiteurs\n      de France est signataire.\n    </p> <p>\n      Il est de la responsabilité́ du client de vérifier l’exhaustivité́ et\n      l’exactitude des informations reportées sur le devis. La dernière\n      version du devis validée provenant du client fait état de référence,\n      MyTerroir ne pourra être tenue responsable d’une erreur validée par le\n      client. Pour être recevable, toute confirmation de commande doit être\n      livrée par écrit 30 jours calendaires avant le jour de la manifestation.\n      Le Client s’engage à fournir au Traiteur gracieusement les fluides\n      nécessaires et notamment l’eau et l’électricité́ à la puissance\n      nécessaire à la bonne réalisation de la Prestation (qui sera précisé́\n      dans le devis). La confirmation est effectuée exclusivement par le\n      client donneur d’ordre et payeur. La validation de commande est\n      matérialisée par l’ensemble des prestations intégrés dans le devis, de\n      ce fait l’ensemble du devis devra être paraphé, la dernière page du\n      budget ainsi que les conditions générales de vente devront être datées,\n      signées, tamponnées, avec les mentions : nom et prénom du donneur\n      d’ordre, « bon pour accord ».\n    </p> <p>\n      Acomptes de réservation : Toute réservation doit être accompagnée du\n      versement d’un acompte correspondant à 80 % du montant du devis total\n      TTC. Le versement de l’acompte déclenche une préparation optimale de\n      l’évènement (réservation du matériel, du personnel de service, achats\n      des matières premières, boissons et éventuelles prestations\n      sous-traitées) ; le versement se décompose comme suit : 30 % à la\n      réservation et 50 % 1 mois avant l’évènement. Le solde devra se faire\n      dans les 15 jours à réception de la facture. d’un chèque de caution de\n      500€ en cas de frais supplémentaires lors de la réception (heures\n      supplémentaires, casse ou manque de matériel). Il vous sera restitué le\n      cas échéant. Si les acomptes ne sont pas perçus en totalité́ dans un\n      délai de 10 jours ouvrés avant la réception, nous nous réservons le\n      droit d’annuler la commande sans qu’aucune indemnité́ pour préjudice ne\n      soit réclamée par le Client. La Clientèle étrangère devra s’acquitter\n      d’un versement d’un acompte de 100 % du montant total soit 60 % à la\n      réservation et 40 % au minimum 10 jours ouvrés avant l’évènement. `\n    </p> <h3>4-2 EXECUTION DU CONTRAT</h3> <p>\n      a/ Informations : MyTerroir fera son affaire de communiquer à ses\n      préposés et intervenants y compris, les modalités d’exécution établies\n      en amont par le client. Il est du devoir du client de prévenir le\n      prestataire de toutes modifications et/ou précisions contractuelles,\n      notamment celles afférentes à la logistique, dans les conditions et\n      selon les modalités stipulées à l’article 4 ci-après.\n    </p> <p>\n      b/ Délégation de pouvoir : Le client donneur d’ordre pourra designer la\n      personne référente sur place le jour J. Le fait pour le client de\n      contracter avec un organisateur ne modifiera en aucun cas ces dites\n      conditions, l’organisateur doit assumer l’ensemble des obligations du\n      client et devra pour ce faire avoir été mandaté par le client et agir au\n      nom et pour le compte de ce dernier.\n    </p> <p>\n      c/ Responsabilité́ du client : Le client reconnait être parfaitement\n      informé et fera son affaire des différents agréments et autorisations\n      administratifs que nécessitent éventuellement l’organisation,\n      l’ouverture au public et le déroulement de sa manifestation.\n    </p> <p>\n      Le client s’engage à prendre toutes dispositions notamment en matière\n      d’assurances pour couvrir tous les risques de toutes natures inhérents à\n      l’évènement, objet de la commande, susceptibles de sécuriser les biens\n      des personnes. Les frais de remise en état des lieux de réception sont\n      pris en charge par le client. Aucune réclamation sur le déroulement de\n      la réception ne sera recevable postérieurement au jour de celle-ci.\n      Toute perte, vol, dégradation, disparition, casse du matériel mise à la\n      disposition des clients n’étant pas inclus dans le tarif du prestataire,\n      seront facturés à leurs prix de remplacement. Le client organisateur est\n      seul responsable des agissements des participants à l’évènement, objet\n      de la commande, il fera son affaire de toute question tenant à la\n      sécurité́ des personnes et des biens, aux lieux où doit se dérouler la\n      manifestation, à la vérification des identités, à l’âge ou à la qualité́\n      des personnes, ainsi qu’à l’autorisation, à la régulation de la\n      consommation des boissons alcoolisées, en aucun cas la responsabilité́ du\n      prestataire ne pourra être recherchée de ces chefs.\n    </p> <p>\n      d/ Qualité́ et hygiène des aliments : Les livraisons sont effectuées par\n      nos soins, en véhicule frigorifique à température règlementaire entre 0\n      °C et 2 °C. Les produits alimentaires sont à conserver entre 0° et 4°C\n      (0° et 2°C pour les produits contenant des produits de la mer crus).\n      Sauf indication contraire, les produits livrés dans le cadre de la\n      Prestation. Le produit doit être consommé dans les 24h après la\n      livraison, dans des conditions optimales de conservation. Suite au\n      décret n° 2002-1465 du 14/12/02, nous vous informons que les viandes\n      bovines sont d'origine : UE. Il appartient au Client de s’assurer que\n      des moyens techniques sont à disposition sur le site pour conserver les\n      produits dans de bonnes conditions de température. Dans l’éventualité́ où\n      le stockage au froid n’est pas possible, les produits alimentaires frais\n      doivent être consommés dans les 2h et avoir été maintenus à une\n      température à cœur inferieure ou égale à + 2°C durant tout ce délai...\n      Si le respect de ce délai de 2h hors froid n’est pas envisageable,\n      MyTerroir peut également conserver un camion frigorifique sur place pour\n      conserver les produits à la demande du Client. Ce service n’est pas\n      systématiquement possible et sera facturé au Client. Si ces conditions\n      de gestion de la température ne sont pas respectées, MyTerroir décline\n      toute responsabilité́ en cas de problème de santé lié à la consommation\n      des produits. Pour des raisons d’hygiène et de sécurité́ alimentaire,\n      aucune marchandise livrée conforme à la commande ne pourra être reprise\n      ou échangée. Par ailleurs, à l’issue de la prestation, notre personnel\n      de service a pour consigne de détruire toutes marchandises restantes.\n      MyTerroir se dégage de toute responsabilité́ sur les risques encourus\n      dans le cas où le Client insisterait pour conserver les produits\n      restants. Une décharge devra, en pareil cas, être signée par le Client.\n    </p> <p>\n      e/ Allergènes : Nos produits contiennent pour certains des allergènes.\n      La liste des allergènes contenus dans des ingrédients volontairement\n      incorporés à la recette est à la disponibilité́ du client à sa demande.\n      Outre, les allergènes volontairement ajoutés, il n’est pas exclu que nos\n      produits contiennent des traces d’un ou plusieurs des allergènes majeurs\n      cités dans l’annexe II du règlement UE 1169/2011. En effet, malgré́\n      toutes les précautions prises durant la préparation, des contaminations\n      croisées fortuites sont possibles.\n    </p> <p>\n      f/ Tri sélectif : Notre engagement en matière de tri, valorisation et\n      recyclage des déchets engendrés par la prestation nécessite la mise à\n      disposition par le client, de contenants de tri sélectifs et un service\n      d’évacuation des déchets. Dans le cas où le client ne serait pas en\n      mesure d’assurer sur site, la gestion des déchets, MyTerroir en assurera\n      la gestion (tri, stockage, évacuation) et des frais supplémentaires\n      s’appliqueront. Des frais seront également à la charge du client, dans\n      le cas où d’éventuels déchets tels que bouteilles en verre, cartons,\n      décoration fournis par le client ou d’autres prestataires sont évacués\n      par les soins de MyTerroir.\n    </p> <p>\n      g/ Lutte contre le gaspillage : En cas d’excédents alimentaires d’un\n      volume significatif, de produits ayant été conservés dans de bonnes\n      conditions de températures (aucune rupture de la chaîne du froid), d’un\n      commun accord avec le client, un don alimentaire peut être effectué :\n    </p> <p>\n      h/ Dépassements horaires : Pour tout service au-delà̀ des horaires\n      convenus entre les parties, un supplément pour le personnel sera\n      facturé, le coût de l’heure supplémentaire par personnel est indiqué́\n      dans le devis. Les heures supplémentaires débuteront à la fin de la\n      vacation du personnel. La vacation du personnel inclue le temps de\n      remise en état des lieux de réception, de rechargement et de transport,\n      toute heure commencée sera facturée en heure pleine.\n    </p> <h3>4-3 LIVRAISON</h3> <p>\n      Aucune livraison ne peut intervenir en l’absence du Client ou d’un\n      réceptionnaire mandaté pour recevoir la commande. L’exactitude et la\n      précision des coordonnées de livraison ainsi que la disponibilité́ du\n      réceptionnaire sont essentielles, tout retard ou erreur ne pourra être\n      imputé a MyTerroir, en cas d’informations erronées ou incomplètes. Les\n      délais prévus dans la livraison d’une commande ne sont donnés qu’à titre\n      indicatif et les retards éventuels ou avances dans l’horaire prévu\n      inferieur à 2 heures ne donnent pas droit au Client d’annuler la\n      commande, de refuser la marchandise ou de réclamer des dommages et\n      intérêts. En cas de redirection de la commande vers une autre adresse de\n      livraison, des frais de livraison supplémentaires s’appliqueront. Toute\n      commande doit être reçue par le Client et contrôlée de manière\n      contradictoire avec le chauffeur au moment de la livraison. Toute\n      réclamation devra être mentionnée, sur le bon de livraison. Les mentions\n      « réserves » ou « sous réserve de déballage » sont dépourvues de portée.\n      En cas de non-conformité́ à la commande ou de défectuosité́, notre\n      garantie se limite au remplacement des marchandises reconnues non\n      conformes ou défectueuses, à l’exclusion de toute autre indemnité́.\n    </p> <h3>4-4 FACTURATION</h3> <p>\n      La facture sera adressée après l’évènement intégrant éventuellement les\n      consommations supplémentaires de boissons, les possibles heures\n      supplémentaires de service et la perte et casse de la vaisselle, du\n      nappage et du matériel mis à votre disposition par MyTerroir. En\n      fonction de ces montants supplémentaires le solde total ou partiel de la\n      caution versée par le client lui sera restitué sous 15 jours\n      calendaires. Les prix indiqués sur le devis correspondent au tarif\n      applicable lors de l’établissement du devis, ils s’entendent HT, sous\n      réserve du taux en vigueur lors de la réception. Compte tenu de la\n      nature des produits que nous commercialisons en circuit court ou\n      assujettis à des hausses de marché (produits frais, alimentaires et\n      périssables) nos tarifs sont susceptibles d’évoluer et d’être modifiés\n      sans préavis. Dans le cas d’un changement de législation, les tarifs\n      seront automatiquement réajustés, par exemple, en cas de variation de\n      taux ou taxes et feront l’objet de la révision de notre devis. Nos prix\n      s'entendent net sans aucune commission ni rétrocession additionnelle\n      postérieure à la signature du contrat. Des frais de gestion (assurance,\n      transport, manutention) sont facturés quand des marchandises appartenant\n      au client sont livrés dans nos entrepôts.\n    </p> <p>\n      Toute rétrocommission pour les sites de réception sera facturée au\n      client. Nos tarifs intègrent la mise à disposition par les sites de\n      réception des offices, des flux et du dispositif de réception de notre\n      matériel ainsi que la mise en avant de notre savoir-faire du fait de\n      notre référencement. Toute rétrocommission ne pourra être pris en compte\n      à partir de la signature du contrat par le donneur d’ordre.\n    </p> <h2>5. CONDITIONS DE MODIFICATION</h2> <h3>5-1 NOMBRE DE PERSONNE DEFINITIVE</h3> <p>\n      Le devis ci-joint a été spécifiquement établi pour un nombre de\n      personnes demandé par le client. Nous nous réservons la possibilité́ de\n      modifier le prix de vente en fonction d’une baisse de nombre de\n      couverts. Pour être recevable, toute demande de modification du nombre\n      de participants doit nous parvenir 7 jours ouvrés avant la date de la\n      manifestation et doit donner lieu à une acceptation écrite de MyTerroir.\n      Une nouvelle version du bon de commande modifié sera alors établie et\n      devra être validée par le même client donneur d’ordre. Si le nombre\n      définitif devait augmenter dans les 7 jours ouvrés précédant la\n      réception, nous ne pourrions assurer les repas qu’en fonction de nos\n      approvisionnements et nous réservons le droit de remplacer les produits\n      tout en gardant la même qualité́.\n    </p> <h3>5-2 ANNULATION</h3> <p>\n      Dans l’hypothèse où le client déciderait d’annuler sa commande pour\n      quelque cause que ce soit, il se verrait soumis au versement d’un dédit\n      suivant l’échéancier suivant : <br>\n      Annulation entre 30 et 60 jours calendaires avant la date de la\n      réception : 15% du montant TTC du devis <br>\n      Annulation entre 20 et 30 jours calendaires avant la date de réception :\n      40 % du montant TTC total du devis <br>\n      Annulation entre 10 et 20 jours calendaires avant la date de réception :\n      60 % du montant TTC total du devis <br>\n      Annulation entre 5 et 10 jours calendaires avant la date de réception :\n      80 % du montant TTC total du devis <br>\n      Annulation à moins de 5 jours calendaires avant la date de réception 100\n      % du montant TTC total du devis.\n    </p> <h3>5-3 REPORT</h3> <p>\n      Tout report, pour quelque raison que ce soit y compris en cas de force\n      majeure, ne peut engendrer la restitution des acomptes déjà̀ encaissés,\n      néanmoins le montant de l’acompte sera attribué́ à la manifestation\n      reportée dans les 12 mois, de même que le report de l’évènement entraine\n      la révision de nos tarifs et du contenu de nos propositions. Dans le cas\n      où, le nombre de repas subirait une variation inferieure à la\n      manifestation initialement prévue, MyTerroir se réserve le droit de\n      modifier le prix unitaire de vente et de facturer des frais techniques,\n      administratifs et de dédommagement auprès de ses propres prestataires.\n    </p> <h2>6. FORCE MAJEURE</h2> <p>\n      MyTerroir est dégagé́ de plein droit de ses obligations sans paiement\n      d’indemnité́ d’aucune sorte en cas de survenance d’un évènement de force\n      majeure ou cas fortuit, notamment en cas de guerre, émeute, blocus grève\n      totale ou partielle, incendie, fermeture administrative par décision de\n      l’état, blocage des moyens de transport, crise sanitaire ou tout autre\n      évènement extérieur susceptible de perturber la bonne circulation des\n      marchandises. De même, la responsabilité́ de MyTerroir ne saurait être\n      engagée pour tout inconvénient ou dommage inhérent à l’utilisation\n      d’internet, notamment une rupture de service, une intrusion extérieure\n      ou un virus informatique. De convention expresse et déterminante à la\n      conclusion du contrat, dans l’hypothèse où la prestation commandée ne\n      pourrait se dérouler à date convenue en raison d’un cas de force\n      majeure, l’acompte versé resterait acquis à MyTerroir, dans les\n      conditions et modalités fixées à l’article 5 susvisé́ ; un avoir\n      correspondant au montant de l’acompte versé valable sur une durée de 12\n      mois suivant son émission serait en pareil cas adressé au client, à\n      l’exclusion de tout remboursement.\n    </p> <h2>7. PROTECTION INTELLECTUELLE</h2> <p>\n      Nous vous rappelons que tout organisateur d’évènement doit s’acquitter\n      des droits de propriété́ intellectuelle liés à son évènement. Les\n      propositions ainsi que les photos, vidéos, plans, supports, maquettes,\n      documents et informations de toutes natures remis ou adressés par\n      MyTerroir au client demeurent la propriété́ de celui- ci, de ce fait ils\n      ne peuvent être communiqués à des tiers sous quelques motifs que se\n      soient. Sauf interdiction formelle du client, le prestataire se réserve\n      le droit d’utiliser des photos de l’évènement, hors personnages et\n      références au nom du client ou des participants, pour sa communication\n      personnelle (plaquette, presse, réseaux sociaux, internet etc...).\n    </p> <h2>8. DONNEES PERSONNELLES ET PROTECTION DE LA VIE PRIVEE</h2> <p>\n      Dans le cadre de ses relations contractuelles, MyTerroir est amené́ à\n      collecter des données à caractère personnel nécessaire au traitement de\n      la commande. Conformément aux dispositions légales et règlementaires\n      applicables, en particulier la loi n° 78-17 du 6 janvier 1978 modifiée\n      relative à l'informatique, aux fichiers et aux libertés et du règlement\n      européen n°2016/679/UE du 27 avril 2016, le client bénéficie d’un droit\n      d’accès, de rectification, de portabilité́ et d’effacement de ses données\n      ou encore de limitation du traitement. Il peut également, pour des\n      motifs légitimes, s’opposer au traitement des données le concernant.\n      Pour exercer ces droits et obtenir des informations détaillées sur les\n      durées de conservation des données, le client doit contacter MyTerroir à\n      l’adresse suivante : 482 rue du courdouney 33140 Cadaujac ou par\n      internet à l’adresse suivante traiteur@my-terroir.fr. Les informations\n      collectées ont pour finalité́ le suivi des commandes, la gestion de la\n      relation client et, si l’utilisateur y a consenti, la prospection\n      clients. L’accès à vos données personnelles est strictement limité au\n      personnel de MyTerroir. Aucun profilage ne sera réalisé́ et plus\n      généralement aucune décision automatisée ne sera prise sur la base des\n      données collectées. Nous nous engageons à ne pas vendre, louer, céder ni\n      donner accès à des tiers aux données du client sans son consentement \n      préalable, à moins d’y être contraint en raison d’un motif légitime\n      (obligation légale, lutte contre la fraude, exercice des droits de la\n      défense, etc.). Le client est informé qu'il dispose du droit\n      d'introduire une action auprès de la Commission Nationale de\n      l'Informatique et des Libertés. Dans le cas de l’utilisation d’une photo\n      de l’évènement à destination des réseaux sociaux du prestataire ou du\n      client, elle fera l’objet d’une validation mutuelle par écrit.\n    </p> <h2>9. LES CONDITIONS DE PAIEMENT</h2> <p>\n      Le solde du prix TTC est réglable 15 jours calendaires avant la\n      réception accompagné d’un chèque de caution de 500€TTC. Les instructions\n      particulières de facturation devront être communiquées au moment de la\n      validation du devis. Le signataire de la modification ne peut récuser\n      avoir accepté́ les présentes conditions sans restriction ni réserves.\n    </p> <h1>CONDITIONS PARTICULIÈRES DE VENTE - LES PARTICULIERS :</h1> <h2>1. ACOMPTE ET REGLEMENT</h2> <p>\n      Échéancier : <br>\n      • Acompte à verser obligatoirement : 80% du montant global TTC 2 mois\n      avant la réception. <br>\n      • Le solde de la facture est à régler impérativement 10 jours\n      calendaires minimum avant la réception avec le versement d’un chèque de\n      caution de 500€ en cas de frais supplémentaires lors de la réception\n      (heures supplémentaires, casse ou manque de matériel). Il vous sera\n      restitué le cas échéant.\n    </p> <h2>2. MENUS SPECIFIQUES</h2> <p>\n      Si le nombre définitif devait augmenter dans les 7 jours ouvrés\n      précèdent la réception, nous ne pourrions assurer les repas qu’en\n      fonction de nos approvisionnements et nous réservons le droit de\n      remplacer les produits tout en gardant la même qualité́. Néanmoins, une\n      majoration de 20% du prix unitaire de ces repas supplémentaires sera\n      appliquée en vertu des coûts supplémentaires (Production, matériel\n      etc...)\n    </p> <p>\n      En vertu des coûts supplémentaires induits par leur production, les\n      menus spécifiques (vegan, végétarien, allergiques etc) seront majorés\n      également de 20%.\n    </p> <h2>3. GESTION DES BOISSONS</h2> <h3>3-1 BOISSONS FOURNIES PAR LE CLIENT</h3> <p>\n      Dans le cas où, avec l’accord du prestataire, le client fournirait les\n      boissons, celles-ci doivent être livrées et récupérées obligatoirement\n      sur le lieu de la réception, à défaut de quoi nous serions obligés de\n      facturer des frais de manutention et de transport.\n    </p> <h3>3-2 DROIT DE GESTION DES BOISSONS DU CLIENT</h3> <p>Le tarif est de 1,70€ TTC par personne.</p> <h3>3-3 GESTION DES BOUTEILLES VIDES</h3> <p>\n      Dans le cas où une benne à verre ne serait pas mise à disposition sur le\n      lieu de réception et dans le cas d’une reprise de ces bouteilles vides\n      par nos soins, un forfait comprenant la remise en carton, le transport,\n      le déchargement et le tri dans nos locaux de ces bouteilles sera\n      appliqué : • • • <br>\n      0 à 50 bouteilles : 55€TTC <br>\n      50 à 100 bouteilles : <br>\n      100€TTC 100 bouteilles et + : 200€TTC\n    </p> <h3>3-4 BOISSONS SPECIFIQUES</h3> <p>\n      • Les préparations maison, cocktails de jus de fruits frais, vins &amp;\n      boissons étrangers ou prévus pour une occasion spécifique et non\n      consommés ne seront pas défalqués de la facturation finale. • Dans le\n      cas de commande de boissons spécifiques hors de notre carte, celles-ci\n      vous seront facturées dans la globalité́ et selon le packaging en vigueur\n      pour ces boissons.\n    </p> <h2>4. GESTION DES DECHETS</h2> <p>\n      • Des bennes de tri sélectifs doivent être mises à disposition du\n      prestataire sur le lieu de réception. • Dans le cas où le prestataire\n      devrait, avec son accord, gérer les déchets du client ou d’autres\n      prestataires, celui-ci se verrait dans l’obligation de facturer des\n      frais de gestion supplémentaires.\n    </p> <h2>5. DEVIS / NOMBRE DE PERSONNES</h2> <p>\n      Le devis en votre possession a été réalisé́ par rapport à un nombre de\n      personnes précis, si celui-ci venait à être modifié, un nouveau devis\n      sera réalisé́.\n    </p> <h1>CONDITIONS GÉNÉRALES DE VENTE – LES MARIAGES :</h1> <p>\n      Les présentes conditions générales ont pour objet de régir les relations\n      entre MyTerroir et le client désigné́ comme donneur d’ordre.\n    </p> <p>\n      En passant commande, le client déclare expressément avoir pris\n      connaissance des conditions générales de vente de MyTerroir et en\n      accepter l’ensemble des dispositions sans réserve, ni restriction.\n    </p> <h2>1. DEFINITIONS</h2> <p>\n      Pour les besoins des présentes conditions générales, les mots suivants\n      débutants par une lettre majuscule auront la signification qui leur est\n      attribuée ci-dessous :\n    </p> <p>\n      Le ou les « Client(s) » : Ce terme désigne toute personne physique ou\n      morale, particulier, désirant faire l’acquisition des Prestations\n      fournies par MyTerroir au titre du particulier.\n    </p> <h2>2. LE DEVIS</h2> <p>\n      L’émission de l’offre de restauration reprend l’ensemble du cahier des\n      charges du client soit la définition du menu, le nombre de convives, la\n      date, les horaires, les prestations annexes demandées ainsi que toutes\n      particularités techniques indispensables à la bonne exécution de la\n      manifestation. La composition de toutes ces données est formalisée par\n      un devis ainsi qu’un prix. Le devis est susceptible d’ajustement suite\n      au repérage du lieu de l’évènement : étage, distance, puissance\n      électrique, équipement de l’office, parking, gestion des déchets,\n      protection des sols, vaisselle imposée par le lieu. Cette liste est non\n      exhaustive.\n    </p> <p>\n      Les prix indiqués sur le devis sont valables pour une période de 3 mois.\n    </p> <h2>3. LE CONTRAT</h2> <p>\n      L’objet du contrat de nature commerciale consiste, en principal, en la\n      fourniture de repas et, à titre accessoire, en la fourniture de\n      prestations annexes. Pour la bonne exécution du contrat, le client\n      s’engage à fournir a MyTerroir les informations nécessaires à la bonne\n      exécution de sa prestation : le programme détaillé́, le plan\n      d’installation des espaces, le détail des services et prestations\n      sollicitées par ailleurs. Le devis établi par MyTerroir constitue les\n      conditions particulières venant compléter les présentes conditions\n      générales. Tous litiges relatifs au présent contrat seront de la\n      compétence exclusive du Tribunal de Commerce de Bordeaux. La\n      constitution du contrat est formalisée par l’acceptation écrite par le\n      client donneur d’ordre, en premier lieu, de la dernière version du devis\n      émis par MyTerroir, en deuxième lieu, des présentes conditions\n      générales. La commande est ferme, définitive et non susceptible\n      d’annulation de part comme de l’autre des deux parties à compter de la\n      réception de sa confirmation.\n    </p> <h2>4. LA COMMANDE</h2> <h3>4-1.FORMALITES DE VALIDATION</h3> <p>\n      Le client s’engage dès la signature du contrat à préciser le nombre de\n      repas au plus juste qui devront être fournis en vertu du pacte national\n      de lutte contre le gaspillage alimentaire dont le réseau des traiteurs\n      de France est signataire.\n    </p> <p>\n      Il est de la responsabilité́ du client de vérifier l’exhaustivité́ et\n      l’exactitude des informations reportées sur le devis. La dernière\n      version du devis validée provenant du client fait état de référence,\n      MyTerroir ne pourra être tenue responsable d’une erreur validée par le\n      client. Pour être recevable, toute confirmation de commande doit être\n      livrée par écrit 30 jours calendaires avant le jour de la manifestation.\n      Le Client s’engage à fournir au Traiteur gracieusement les fluides\n      nécessaires et notamment l’eau et l’électricité́ à la puissance\n      nécessaire à la bonne réalisation de la Prestation (qui sera précisé́\n      dans le devis). La confirmation est effectuée exclusivement par le\n      client donneur d’ordre et payeur. La validation de commande est\n      matérialisée par l’ensemble des prestations intégrés dans le devis, de\n      ce fait l’ensemble du devis devra être paraphé, la dernière page du\n      budget ainsi que les conditions générales de vente devront être datées,\n      signées, tamponnées, avec les mentions : nom et prénom du donneur\n      d’ordre, « bon pour accord ».\n    </p> <p>\n      Acomptes de réservation : Toute réservation doit être accompagnée du\n      versement d’un acompte correspondant à 100 % du montant du devis total\n      TTC ainsi qu’une caution couvrant les éventuels frais supplémentaires.\n      Échéancier : <br>\n\n      Des arrhes, d’un montant de 3000.00€ sont à verser obligatoirement afin\n      de bloquer définitivement la date de votre réception sur notre planning.\n      <br>\n      Acompte à verser obligatoirement : 80% du montant global TTC 2 mois\n      avant la réception.\n      <br>\n      Le solde de la facture est à régler impérativement 10 jours calendaires\n      minimum avant la réception avec le versement d’un chèque de caution de\n      500€ en cas de frais supplémentaires lors de la réception (heures\n      supplémentaires, casse ou manque de matériel). Il vous sera restitué le\n      cas échéant.\n    </p> <h3>4-2 EXECUTION DU CONTRAT</h3> <p>\n      a/ Informations : MyTerroir fera son affaire de communiquer à ses\n      préposés et intervenants y compris, les modalités d’exécution établies\n      en amont par le client. Il est du devoir du client de prévenir le\n      prestataire de toutes modifications et/ou précisions contractuelles,\n      notamment celles afférentes à la logistique, dans les conditions et\n      selon les modalités stipulées à l’article 4 ci-après.\n    </p> <p>\n      b/ Délégation de pouvoir : Le client donneur d’ordre pourra designer la\n      personne référente sur place le jour J. Le fait pour le client de\n      contracter avec un organisateur ne modifiera en aucun cas ces dites\n      conditions, l’organisateur doit assumer l’ensemble des obligations du\n      client et devra pour ce faire avoir été mandaté par le client et agir au\n      nom et pour le compte de ce dernier.\n    </p> <p>\n      c/ Responsabilité́ du client : Le client reconnait être parfaitement\n      informé et fera son affaire des différents agréments et autorisations\n      administratifs que nécessitent éventuellement l’organisation,\n      l’ouverture au public et le déroulement de sa manifestation. Le client\n      sera seul responsable des perturbations du service de MyTerroir dans le\n      cadre de changement d’horaire lors du déroulement de l’évènement. Le\n      client s’engage à prendre toutes dispositions notamment en matière\n      d’assurances pour couvrir tous les risques de toutes natures inhérents à\n      l’évènement, objet de la commande, susceptibles de sécuriser les biens\n      des personnes. Les frais de remise en état des lieux de réception sont\n      pris en charge par le client. Aucune réclamation sur le déroulement de\n      la réception ne sera recevable postérieurement au jour de celle-ci.\n      Toute perte, vol, dégradation, disparition, casse du matériel mise à la\n      disposition des clients n’étant pas inclus dans le tarif du prestataire,\n      seront facturés à leurs prix de remplacement. Le client organisateur est\n      seul responsable des agissements des participants à l’évènement, objet\n      de la commande, il fera son affaire de toute question tenant à la\n      sécurité́ des personnes et des biens, aux lieux où doit se dérouler la\n      manifestation, à la vérification des identités, à l’âge ou à la qualité́\n      des personnes, ainsi qu’à l’autorisation, à la régulation de la\n      consommation des boissons alcoolisées, en aucun cas la responsabilité́ du\n      prestataire ne pourra être recherchée de ces chefs.\n    </p> <p>\n      d/ Qualité́ et hygiène des aliments : Les livraisons sont effectuées par\n      nos soins, en véhicule frigorifique à température règlementaire entre 0\n      °C et 2 °C. Les produits alimentaires sont à conserver entre 0° et 4°C\n      (0° et 2°C pour les produits contenant des produits de la mer crus). Il\n      appartient au client de s’assurer que des moyens techniques sont à\n      disposition sur le site pour conserver les produits dans de bonnes\n      conditions de température. MyTerroir peut également conserver un camion\n      frigorifique sur place pour conserver les produits à la demande du\n      Client. Ce service n’est pas systématiquement possible et sera facturé\n      au Client. Si ces conditions de gestion de la température ne sont pas\n      respectées, MyTerroir décline toute responsabilité́ en cas de problème de\n      santé lié à la consommation des produits. Par ailleurs, à l’issue de la\n      prestation, notre personnel de service a pour consigne de détruire\n      toutes marchandises restantes. Le prestataire se dégage de toute\n      responsabilité́ sur les risques encourus dans le cas où le client\n      insisterait pour conserver les produits restants moyennant une\n      attestation de décharge signée par lui.\n    </p> <p>\n      e/ Allergènes : Nos produits contiennent pour certains des allergènes.\n      La liste des allergènes contenus dans des ingrédients volontairement\n      incorporés à la recette est à la disponibilité́ du client à sa demande.\n      Outre, les allergènes volontairement ajoutés, il n’est pas exclu que nos\n      produits contiennent des traces d’un ou plusieurs des allergènes majeurs\n      cités dans l’annexe II du règlement UE 1169/2011. En effet, malgré́\n      toutes les précautions prises durant la préparation, des contaminations\n      croisées fortuites sont possibles.\n    </p> <p>\n      f/ Tri sélectif : Notre engagement en matière de tri, valorisation et\n      recyclage des déchets engendrés par la prestation nécessite la mise à\n      disposition par le client, de contenants de tri sélectifs et un service\n      d’évacuation des déchets. Dans le cas où le client ne serait pas en\n      mesure d’assurer sur site, la gestion des déchets, MyTerroir en assurera\n      la gestion (tri, stockage, évacuation) et des frais supplémentaires\n      s’appliqueront. Des frais seront également à la charge du client, dans\n      le cas où d’éventuels déchets tels que bouteilles en verre, cartons,\n      décoration fournis par le client ou d’autres prestataires sont évacués\n      par les soins de MyTerroir.\n    </p> <p>\n      g/ Lutte contre le gaspillage : En cas d’excédents alimentaires d’un\n      volume significatif, de produits ayant été conservés dans de bonnes\n      conditions de températures (aucune rupture de la chaîne du froid), d’un\n      commun accord avec le client, un don alimentaire peut être effectué.\n    </p> <p>\n      h/ Dépassements horaires : Pour tout service au-delà̀ des horaires\n      convenus entre les parties, un supplément pour le personnel sera\n      facturé, le coût de l’heure supplémentaire par personnel est indiqué́\n      dans le devis. Les heures supplémentaires débuteront à la fin de la\n      vacation du personnel. La vacation du personnel inclue le temps de\n      remise en état des lieux de réception, de rechargement et de transport,\n      toute heure commencée sera facturée en heure pleine.\n    </p> <h3>4-4 FACTURATION</h3> <p>\n      La facture sera adressée après l’évènement intégrant éventuellement les\n      consommations supplémentaires de boissons, les possibles heures\n      supplémentaires de service et la perte et casse de la vaisselle, du\n      nappage et du matériel mis à votre disposition par MyTerroir. En\n      fonction de ces montants supplémentaires le solde total ou partiel de la\n      caution versée par le client lui sera restitué sous 15 jours\n      calendaires. Les prix indiqués sur le devis correspondent au tarif\n      applicable lors de l’établissement du devis, ils s’entendent HT, sous\n      réserve du taux en vigueur lors de la réception. Compte tenu de la\n      nature des produits que nous commercialisons en circuit court ou\n      assujettis à des hausses de marché (produits frais, alimentaires et\n      périssables) nos tarifs sont susceptibles d’évoluer et d’être modifiés\n      sans préavis. Dans le cas d’un changement de législation, les tarifs\n      seront automatiquement réajustés, par exemple, en cas de variation de\n      taux ou taxes et feront l’objet de la révision de notre devis. Nos prix\n      s'entendent net sans aucune commission ni rétrocession additionnelle\n      postérieure à la signature du contrat. Des frais de gestion (assurance,\n      transport, manutention) sont facturés quand des marchandises appartenant\n      au client sont livrés dans nos entrepôts. Toute rétrocommission pour les\n      sites de réception sera facturée au client. Nos tarifs intègrent la mise\n      à disposition par les sites de réception des offices, des flux et du\n      dispositif de réception de notre matériel ainsi que la mise en avant de\n      notre savoir-faire du fait de notre référencement. Toute rétrocommission\n      ne pourra être pris en compte à partir de la signature du contrat par le\n      donneur d’ordre.\n    </p> <h2>5. CONDITIONS DE MODIFICATION</h2> <h3>5-1 NOMBRE DE PERSONNE DEFINITIVE</h3> <p>\n      Le devis ci-joint a été spécifiquement établi pour un nombre de\n      personnes demandé par le client. Nous nous réservons la possibilité́ de\n      modifier le prix de vente en fonction d’une baisse de nombre de\n      couverts. Pour être recevable, toute demande de modification du nombre\n      de participants doit nous parvenir 7 jours ouvrés avant la date de la\n      manifestation et doit donner lieu à une acceptation écrite de MyTerroir.\n      Une nouvelle version du bon de commande modifié sera alors établie et\n      devra être validée par le même client donneur d’ordre. Si le nombre\n      définitif devait augmenter dans les 7 jours ouvrés précédant la\n      réception, nous ne pourrions assurer les repas qu’en fonction de nos\n      approvisionnements et nous réservons le droit de remplacer les produits\n      tout en gardant la même qualité́.\n    </p> <h3>5-2 ANNULATION</h3> <p>\n      Dans l’hypothèse où le client déciderait d’annuler sa commande pour\n      quelque cause que ce soit, il se verrait soumis au versement d’un dédit\n      suivant l’échéancier suivant : <br>\n      Annulation plus de 60 jours avant la date de la réception : Montant des\n      arrhes versées ou dus à la réception à savoir 3000€ TTC. <br>\n      Annulation entre 30 et 60 jours calendaires avant la date de la\n      réception : 15% du montant TTC du devis <br>\n      Annulation entre 20 et 30 jours calendaires avant la date de réception :\n      40 % du montant TTC total du devis <br>\n      Annulation entre 10 et 20 jours calendaires avant la date de réception :\n      60 % du montant TTC total du devis <br>\n      Annulation entre 5 et 10 jours calendaires avant la date de réception :\n      80 % du montant TTC total du devis <br>\n      Annulation à moins de 5 jours calendaires avant la date de réception 100\n      % du montant TTC total du devis.\n    </p> <h3>5-3 REPORT</h3> <p>\n      Tout report, pour quelque raison que ce soit y compris en cas de force\n      majeure, ne peut engendrer la restitution des acomptes déjà̀ encaissés,\n      néanmoins le montant de l’acompte sera attribué́ à la manifestation\n      reportée dans les 12 mois, de même que le report de l’évènement entraine\n      la révision de nos tarifs et du contenu de nos propositions. Dans le cas\n      où, le nombre de repas subirait une variation inferieure à la\n      manifestation initialement prévue, MyTerroir se réserve le droit de\n      modifier le prix unitaire de vente et de facturer des frais techniques,\n      administratifs et de dédommagement auprès de ses propres prestataires.\n    </p> <h2>6. FORCE MAJEURE</h2> <p>\n      MyTerroir est dégagé́ de plein droit de ses obligations sans paiement\n      d’indemnité́ d’aucune sorte en cas de survenance d’un évènement de force\n      majeure ou cas fortuit, notamment en cas de guerre, émeute, blocus grève\n      totale ou partielle, incendie, fermeture administrative par décision de\n      l’état, blocage des moyens de transport, crise sanitaire ou tout autre\n      évènement extérieur susceptible de perturber la bonne circulation des\n      marchandises. De même, la responsabilité́ de MyTerroir ne saurait être\n      engagée pour tout inconvénient ou dommage inhérent à l’utilisation\n      d’internet, notamment une rupture de service, une intrusion extérieure\n      ou un virus informatique. De convention expresse et déterminante à la\n      conclusion du contrat, dans l’hypothèse où la prestation commandée ne\n      pourrait se dérouler à date convenue en raison d’un cas de force\n      majeure, l’acompte versé resterait acquis à MyTerroir, dans les\n      conditions et modalités fixées à l’article 5 susvisé́ ; un avoir\n      correspondant au montant de l’acompte versé valable sur une durée de 12\n      mois suivant son émission serait en pareil cas adressé au client, à\n      l’exclusion de tout remboursement.\n    </p> <h2>7. PROTECTION INTELLECTUELLE</h2> <p>\n      Nous vous rappelons que tout organisateur d’évènement doit s’acquitter\n      des droits de propriété́ intellectuelle liés à son évènement. Les\n      propositions ainsi que les photos, vidéos, plans, supports, maquettes,\n      documents et informations de toutes natures remis ou adressés par\n      MyTerroir au client demeurent la propriété́ de celui- ci, de ce fait ils\n      ne peuvent être communiqués à des tiers sous quelques motifs que se\n      soient. Sauf interdiction formelle du client, le prestataire se réserve\n      le droit d’utiliser des photos de l’évènement, hors personnages et\n      références au nom du client ou des participants, pour sa communication\n      personnelle (plaquette, presse, réseaux sociaux, internet etc...).\n    </p> <h2>8. DONNEES PERSONNELLES ET PROTECTION DE LA VIE PRIVEE</h2> <p>\n      Dans le cadre de ses relations contractuelles, MyTerroir est amené́ à\n      collecter des données à caractère personnel nécessaire au traitement de\n      la commande. Conformément aux dispositions légales et règlementaires\n      applicables, en particulier la loi n° 78-17 du 6 janvier 1978 modifiée\n      relative à l'informatique, aux fichiers et aux libertés et du règlement\n      européen n°2016/679/UE du 27 avril 2016, le client bénéficie d’un droit\n      d’accès, de rectification, de portabilité́ et d’effacement de ses données\n      ou encore de limitation du traitement. Il peut également, pour des\n      motifs légitimes, s’opposer au traitement des données le concernant.\n      Pour exercer ces droits et obtenir des informations détaillées sur les\n      durées de conservation des données, le client doit contacter MyTerroir à\n      l’adresse suivante : 482 rue du courdouney 33140 Cadaujac ou par\n      internet à l’adresse suivante traiteur@my-terroir.fr. Les informations\n      collectées ont pour finalité́ le suivi des commandes, la gestion de la\n      relation client et, si l’utilisateur y a consenti, la prospection\n      clients. L’accès à vos données personnelles est strictement limité au\n      personnel de MyTerroir. Aucun profilage ne sera réalisé́ et plus\n      généralement aucune décision automatisée ne sera prise sur la base des\n      données collectées. Nous nous engageons à ne pas vendre, louer, céder ni\n      donner accès à des tiers aux données du client sans son consentement\n      préalable, à moins d’y être contraint en raison d’un motif légitime\n      (obligation légale, lutte contre la fraude, exercice des droits de la\n      défense, etc.). Le client est informé qu'il dispose du droit\n      d'introduire une action auprès de la Commission Nationale de\n      l'Informatique et des Libertés. Dans le cas de l’utilisation d’une photo\n      de l’évènement à destination des réseaux sociaux du prestataire ou du\n      client, elle fera l’objet d’une validation mutuelle par écrit.\n    </p> <h2>9. LES CONDITIONS DE PAIEMENT</h2> <p>\n      Le solde du prix TTC est réglable 15 jours calendaires avant la\n      réception accompagné d’un chèque de caution de 500€TTC. Les instructions\n      particulières de facturation devront être communiquées au moment de la\n      validation du devis. Le signataire de la modification ne peut récuser\n      avoir accepté́ les présentes conditions sans restriction ni réserves.\n    </p> <h1>CONDITIONS PARTICULIÈRES DE VENTE – LES MARIAGES :</h1> <h2>1. ACOMPTE ET REGLEMENT</h2> <p>\n      Échéancier : <br>\n\n      Des arrhes, d’un montant de 3000.00 € sont à verser obligatoirement afin\n      de bloquer définitivement la date de votre réception sur notre planning.\n      <br>\n      Acompte à verser obligatoirement : 80% du montant global TTC 2 mois\n      avant la réception. <br>\n      Le solde de la facture est à régler impérativement 15 jours calendaires\n      minimum avant la réception avec le versement d’un chèque de caution de\n      3000€ en cas de frais supplémentaires lors de la réception (heures\n      supplémentaires, casse ou manque de matériel). Il vous sera restitué le\n      cas échéant.\n    </p> <h2>2. REPAS DEGUSTATION</h2> <p>\n      Afin que le client valide l’offre commerciale, un test de dégustation de\n      6 personnes au maximum pourra être réalisé́ par MyTerroir. Le client\n      s’engage à respecter les conditions suivantes : Le coût unitaire de ces\n      repas est de 50.00€TTC et doit être réglé́ sur place le jour même. Ce\n      montant ne sera pas remboursé si MyTerroir n’est pas choisi pour\n      l’organisation de cette réception et sera considéré́ comme un acompte\n      dans le cas contraire. Pour les mariages dont le nombre est inferieur ou\n      égal à 60 personnes, les repas dégustations sont payants. Le repas du ou\n      de la Wedding planner sera offert. Les repas dégustations auront lieu\n      dans nos locaux au 482 rue du courdouney à Cadaujac du lundi au vendredi\n      à l’heure du déjeuner.\n    </p> <h2>3. MENUS SPECIFIQUES</h2> <p>\n      Si le nombre définitif devait augmenter dans les 7 jours ouvrés\n      précèdent la réception, nous ne pourrions assurer les repas qu’en\n      fonction de nos approvisionnements et nous réservons le droit de\n      remplacer les produits tout en gardant la même qualité́. Néanmoins, une\n      majoration de 20% du prix unitaire de ces repas supplémentaires sera\n      appliquée en vertu des coûts supplémentaires (Production, matériel\n      etc...). En vertu des coûts supplémentaires induits par leur production,\n      les menus spécifiques (vegan, végétarien, allergiques etc) seront\n      majorés également de 20%.\n    </p> <h2>4. GESTION DES BOISSONS</h2> <h3>4-1 BOISSONS FOURNIES PAR LE CLIENT</h3> <p>\n      Dans le cas où, avec l’accord du prestataire, le client fournirait les\n      boissons, celles-ci doivent être livrées et récupérées obligatoirement\n      sur le lieu de la réception, à défaut de quoi nous serions obligés de\n      facturer des frais de manutention et de transport.\n    </p> <h3>4-2 DROIT DE GESTION DES BOISSONS DU CLIENT</h3> <p>Le tarif est de 1,70€ TTC par personne.</p> <h3>4-3 GESTION DES BOUTEILLES VIDES</h3> <p>\n      Dans le cas où une benne à verre ne serait pas mise à disposition sur le\n      lieu de réception et dans le cas d’une reprise de ces bouteilles vides\n      par nos soins, un forfait comprenant la remise en carton, le transport,\n      le déchargement et le tri dans nos locaux de ces bouteilles sera\n      appliqué : <br>\n      0 à 50 bouteilles : 55€TTC 50 à 100 bouteilles : 100€TTC <br>\n      100 bouteilles et + : 200€TTC\n    </p> <h3>4-4 BOISSONS SPECIFIQUES</h3> <p>\n      Les préparations maison, cocktails de jus de fruits frais, vins &amp;\n      boissons étrangers ou prévus pour une occasion spécifique et non\n      consommés ne seront pas défalqués de la facturation finale. Dans le cas\n      de commande de boissons spécifiques hors de notre carte, celles-ci vous\n      seront facturées dans la globalité́ et selon le packaging en vigueur pour\n      ces boissons.\n    </p> <h2>5. GESTION DES DECHETS</h2> <p>\n      Des bennes de tri sélectifs doivent être mises à disposition du\n      prestataire sur le lieu de réception. Dans le cas où le prestataire\n      devrait, avec son accord, gérer les déchets du client ou d’autres\n      prestataires, celui-ci se verrait dans l’obligation de facturer des\n      frais de gestion supplémentaires.\n    </p> <h2>6. DEVIS / NOMBRE DE PERSONNES</h2> <p>\n      Le devis en votre possession a été réalisé́ par rapport à un nombre de\n      personnes précis, si celui-ci venait à être modifié, un nouveau devis\n      sera réalisé́.\n    </p></section> "),_c('Newsletter')],2)}
