export default {
  data() {
    return {
      form: { email: '' },
      loading: false,
      state: true,
      goodNews: false,
      error: false
    }
  },
  methods: {
    subscribe(e) {
      e.preventDefault()
      console.log({ ...this.form })
      ;(this.loading = true), (this.state = false)
      this.$axios
        .post('https://apimyterroir.rouxnicolas.fr/subscribe', { ...this.form })
        .then(
          (res) => (
            (this.form = ''),
            (this.loading = false),
            (this.state = true),
            (this.goodNews = true)
          )
        )
        .catch((error) => {
          ;(this.error = true),
            (this.form = ''),
            (this.loading = false),
            (this.state = true)
        })
    }
  }
}
